#Search {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    margin-bottom: 10px;
    font-size: medium;
  }

  input {
    height: 30px;
    width: 50%;
    border-radius: 2px;
    background-color: rgb(237, 244, 249);
    outline: none;
    border-style: solid;
  }

  input:focus {
    border-color: skyblue;
    transition: 0.3s ease;
  }
  